import React from 'react';
import { Modal, Divider, Descriptions, List, Avatar } from 'antd';
import { format } from 'date-fns';

import { useConfigCache } from '../../../utils/Constants';


const OrderInfo = (props) => {
  const { order, closeModal, visible, ... restProps } = props;
  const config = useConfigCache();

  const orderItems = (item) => {
    let title = item.product.name;
    let variant = "";
    let variantKeys = Object.keys(item.variant);
    variantKeys.map((aKey, index)=>{
      variant += `${aKey}: ${item.variant[aKey]}${index == variantKeys.length -1 ? "" : ", "}`
    })

    let imageSrc = "";
    if (config && item.product.image) {
      imageSrc = config.imageSrc + item.product.image;
    }
    return (
      <List.Item
        actions={[
          "qty: " + item.qty,
          "price: " + item.price
        ]}
      >
        <List.Item.Meta
          avatar={
            <Avatar shape="square" src={imageSrc} />
          }
          title={title}
          description={variant}
        />
      </List.Item>
    )
  }
  return (
    <Modal
      title={"Order"}
      width={'95%'}
      visible={visible}
      onCancel={closeModal}
      footer={null}
      //destroyOnClose
      wrapClassName={'products-modalWrapper'}
      style={{overflow:"hidden"}}
    >
    {
      order ?
      <React.Fragment>
        <Divider orientation="left"></Divider>
        <Descriptions 
            size="small"
            bordered
            column={{ xxl: 2, xl: 2, lg: 2, md: 2, sm: 1, xs: 1 }}
        >
            <Descriptions.Item label="">{order._id}</Descriptions.Item>
            <Descriptions.Item label="">{format(new Date(order.createdAt), "MM/dd/yyyy hh:mm:ss aa")}</Descriptions.Item>
            <Descriptions.Item label="">
                {
                  order.paid ? 
                    <span style={{"color":"green"}}></span> : <span style={{"color":"red"}}></span>
                }
            </Descriptions.Item>
            <Descriptions.Item label="">{order.sentOut ? <span style={{"color":"green"}}> <small> (: {order.trackingNum})</small></span>:<span style={{"color":"red"}}></span>}</Descriptions.Item>
        </Descriptions>
        <Divider orientation="left"></Divider>
        <Descriptions 
            id="buyerInfoTable"
            size="small"
            bordered
            column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
        >
            <Descriptions.Item label="">{order.customer.name}</Descriptions.Item>
            <Descriptions.Item label="">contact</Descriptions.Item>
            <Descriptions.Item label="">address</Descriptions.Item>
        </Descriptions>
        <Divider orientation="left"></Divider> 
        <List
          itemLayout="horizontal"
          dataSource={order.items}
          renderItem={orderItems}
          //bordered
          footer={(
            <div className="orderInfo-item-summary">
              Total: {order.total}
            </div>
          )}
        />
      </React.Fragment>
      : "Not found"
    }
  
    </Modal>
  )
}

export default OrderInfo;